      getCoordinates() {
        console.log('getCoordinates')
        let result = false
        window.navigator.geolocation.getCurrentPosition((postion) => {
          const { coords } = postion
          this.coords = [coords.latitude, coords.longitude]
          result = true
        }, (positionError) => {
          console.log('positionError', positionError)
        })
        return result
      },
      stopInterval() {
        window.clearInterval(this.intervalId)
      },
